@font-face {
  font-family: "Roboto", sans-serif;
  src: url("../fonts/Roboto.ttf") format("truetype");
}
@font-face {
  font-family: "Roboto", sans-serif;
  font-weight: bold;
  src: url("../fonts/Robotobold.ttf") format("truetype");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.display-none {
  display: none;
}
.container {
  width: fit-content;
  margin: 0 auto;
}
.button {
  color: #ffffff;
  text-decoration: none;
  border: none;
  background: #000000;
  padding: 15px 5px;
  width: 100%;
}
html {
  height: 100%;
}
.main-page {
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  margin: 0;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 400;
  background-color: #ffffff;
  color: #000000;
}
.main-container__title {
  text-align: center;
  margin-bottom: 70px;
}
.main-title-span {
  color: #008000;
}
